var graphics = require('./graphics');

var pacman = new Path2D(`
    M 0 0
    L .4504 .2169
    A .5 .5 0 1 1 .4504 -.2169
    L 0 0
`);
var outer_pacman_eye = new Path2D(`
    M 0 0
    A .1428 .1428 0 1 1 0 .0001
`);
var inner_pacman_eye = new Path2D(`
    M 0 0
    A .0625 .0625 0 1 1 0 .0001
`);

graphics.push();
graphics.translate([100, 150]);
graphics.scale([100, 100]);
graphics.setPen({
    width: 1/50
});
graphics.setBrush({
    color: '#ffdf00'
});
graphics.fillPath(pacman);
graphics.drawPath(pacman);
graphics.push();
graphics.translate([-1/7, -2/7]);
graphics.setBrush({
    color: '#fff'
});
graphics.fillPath(outer_pacman_eye);
graphics.drawPath(outer_pacman_eye);
graphics.pop();
graphics.translate([-1/16, -2/7]);
graphics.setBrush({
    color: '#000'
});
graphics.fillPath(inner_pacman_eye);

var ghost = new Path2D(`
    M 0 1
    V .3333
    C 0 .0833, .3333 0, .5 0
    S 1 .0833, 1 .3333
    V 1
    L .8333 .7777
    L .6666 1
    L .5 .7777
    L .3333 1
    L .1666 .7777
    L 0 1
`);

var outer_ghost_eye = new Path2D();
outer_ghost_eye.arc(0, 0,  1/7, 0, Math.PI*2);

var inner_ghost_eye = new Path2D();
inner_ghost_eye.arc(0, 0, 1/14, 0, Math.PI*2);

graphics.pop();
graphics.translate([300, 100]);
graphics.scale([100, 100]);
graphics.setPen({
    width: 1/50
});
graphics.setBrush({
    color: '#abcdef'
});
graphics.fillPath(ghost);
graphics.drawPath(ghost);

graphics.translate([9/32, 13/32]);
graphics.setBrush({
    color: '#fff'
});
graphics.fillPath(outer_ghost_eye);
graphics.drawPath(outer_ghost_eye);
graphics.setBrush({
    color: '#000'
});
graphics.fillPath(inner_ghost_eye);

graphics.translate([14/32, 0]);
graphics.setBrush({
    color: '#fff'
});
graphics.fillPath(outer_ghost_eye);
graphics.drawPath(outer_ghost_eye);
graphics.setBrush({
    color: '#000'
});
graphics.fillPath(inner_ghost_eye);
